import React from 'react';
import {DataType} from '../../dal/api';
import {Employee} from './employee/Employee';
import {Field, Form, Formik} from 'formik';
import {initialValuesType} from './EmployeesContainer';
import s from './Employees.module.css'
import {Button} from '@material-ui/core';


export const Employees: React.FC<PropsType> = ({employees, deleteEmployeesCallback, initialValues, onSubmit}) => {

    const mappedEmployees = employees.map((tl: DataType) => (
        <Employee key={tl.id} deleteEmployeesCallback={deleteEmployeesCallback} employee={tl}/>
    ))
    return (
        <div className={s.wrapperEmployees}>
            {mappedEmployees}
            <Formik initialValues={initialValues} onSubmit={onSubmit}>
                <Form className={s.form}>
                    <Field type="text" name="first_name"/>
                    <Field type="text" name="last_name"/>
                    <Field type="text" name="email"/>
                    <Button variant="contained" color="primary" type="submit">Submit</Button>
                </Form>

            </Formik>

        </div>
    );
}
//types
type PropsType = {
    employees: DataType[]
    deleteEmployeesCallback: (id: number | string) => void
    initialValues: initialValuesType
    onSubmit: (values: initialValuesType) => void
}

